    background-position: center;
}
.about-img-container{
    width: 45%;
    position: relative;
}
.about-img-container img{
    width: 100%;
    object-fit: contain;
}
.floating-about-img-container{
    position: absolute;
    background-color: var(--secondary-color);
    padding: 10px 30px;
    opacity: .9;
    top: -30px;
    left: -200px;
}
.floating-about-img-container2{
    position: absolute;
    background-color: var(--secondary-color);
    padding: 10px 30px;
    opacity: .9;
    bottom: -30px;
    right: -60px;
}
.about-text{
    width: 50%;
}
.about-text p{
    width: 95%;
    margin-top: 20px;
}
.about-wrapper{
    width: 85%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
@media (max-width: 990px) and (min-width: 768px){
    .about-wrapper{
        flex-direction: column;
    }
    .about-img-container{
        width: 100%;
    }
    .about-text{
        width: 100%;
        margin-bottom: 40px;
    }
    .launch{
        width: 100%;
        justify-content: center;
    }
}
@media screen and (max-width: 767px){
    .about-text{
        width: 100%;
        margin-bottom: 40px;
        height: auto;
    }
    .launch{
        width: 100%;
        justify-content: center;
    }
    .about-img-container{
        width: 100%;
    }
    .floating-about-img-container{
        position: static;
        margin: 15px 0px 20px 0px;
    }
    .floating-about-img-container2{
        position: static;
    }
    .about-img{
        object-fit: cover;
    }
}