.feature-wrapper{
    flex-direction: column;
    align-items: flex-start;
}
.feature-card-container{
    width: 100%;
    /* background-color: red; */
    align-items: center;
    justify-content: space-between;
    padding-top: 40px;
    flex-wrap: wrap;
    flex-direction: row;
    display: flex;
}
