}
.feature-card{
    width: 45%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 10px 0px;
    text-align: left;
}
.feature-card h2{
    font-weight: 400;
    color: var(--primary-color);
    text-transform: capitalize;
    font-size: 1.4rem;
}
.feature-card p{
    color: var(--mild-color);
}
.block-img{
    height: 100px;
    object-fit: contain;
    margin: 10px 0px;
}
.feature-card-text-container svg{
    font-size: 3rem;
    color: var(--mild-color);
}
.feature-card-text-container h2{
    color: rgb(4, 234, 246);
    margin: 15px 0;
}
@media screen and (max-width: 767px){
    .feature-card-container{
        margin-top: 0px;
    }
    .feature-card{
        width: 100%;
        margin: 30px 0px;
    }
}