*{
  padding: 0px;
  border: 0px;
  text-decoration: none;
  margin: 0px;
  box-sizing: border-box;
  list-style: none;
  font-family: lato,'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
html{
  scroll-behavior: smooth;
  overflow-x: hidden;
}
body{
  scroll-behavior: smooth;
  overflow-x: hidden;
  position: relative;
}
:root{
  --primary-color:rgb(227,246,245);
  --secondary-color:rgb(39,38,67);
  --mild-color:#ddd;
  --color-variant:rgb(4, 234, 246);
}
h1{
  font-family: poppins, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 2.5rem;
  text-transform: capitalize;
}
h2{
  font-family: poppins, roboto;
  font-size: 1.5rem;
}
h3{
  font-size: 1.5rem;
  font-weight: 500;
  font-family: montserrat;
  text-transform: capitalize;
  color: #0b4c89;
}
section{
  width: 85%;
  display: flex;
  padding: 10px;
  align-items: center;
}
p{
  font-size: 1rem;
  font-family: roboto;
  line-height: 25px;
}
a{
  font-family: poppins;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
  width: 100%;
}
.header h2{
  font-weight: 400;
