.plan-card-container{
    width: 85%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 0px;
    flex-wrap: wrap;
}
.container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .card_box {
    width: 300px;
    height: auto;
    border-radius: 5px;
    background:rgb(13, 102, 238);
    position: relative;
    cursor: pointer;
    transition: all .3s;
    margin-top: 40px;
    padding: 10px 0;
  }
  
  .card_box:hover {
    transform: scale(0.9);
  }
  
  .card_box span {
    position: absolute;
    overflow: hidden;
    width: 150px;
    height: 150px;
    top: -10px;
    left: -10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
 .box1 span::before {
    content: 'basic plan';
    position: absolute;
    width: 150%;
    height: 40px;
    background-image: linear-gradient(45deg, rgb(39,38,67) 0%, rgb(4, 234, 246)  51%, rgb(227,246,245)  100%);
    transform: rotate(-45deg) translateY(-20px);
    display: flex;
    align-items: center;
    font-family: poppins;
    justify-content: center;
    color: var(--secondary-color);
    font-weight: 600;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    box-shadow: 0 5px 10px rgba(0,0,0,0.23);
  }
  .box2 span::before {
    content: 'mega plan';
