  }
  
  .card_box span::after {
    content: '';
    position: absolute;
    width: 10px;
    bottom: 0;
    left: 0;
    height: 10px;
    z-index: -1;
    box-shadow: 140px -140px  rgb(39,38,67);
    background-image: linear-gradient(45deg, rgb(39,38,67) 0%, rgb(4, 234, 246)  51%, rgb(227,246,245)  100%);
  }
  .my-plan-container{
    display: flex;
    flex-direction: column;
    padding: 10px;
    align-items: center;
    justify-content: center;
    background-color: white;
    width: 30%;
    margin-top: 20px;
  }
