.roadmap-container{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 60px 0px;
}
.flipright-card{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.flipleft-card{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.counter{
    width: 40%;
    text-transform: capitalize;
    align-self: flex-start;
    text-align: left;
}
.counter h1{
    font-size: 4rem;
    color: var(--mild-color);
}
.counter h2{
    color: var(--primary-color);
    font-weight: 400;
}
.msg-box{
    height: 250px;
    background-color: white;
    clip-path: polygon(5% 0, 100% 0%, 100% 100%, 66% 100%, 4% 100%, 4% 10%, 0 0);
    width: 50%;
    align-self: flex-start;
    border-radius: 10px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding:20px 0px;
}
.line-container{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
.ball{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: var(--color-variant);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    color: var(--secondary-color);
}
.msg-box{
    padding: 20px;
    height: auto;
}
.msg-box h2{
    text-transform: capitalize;
    font-weight: 400;
    color: rgb(2, 79, 109);
    width: 80%;
    text-align: left;
}
.msg-box ul {
    margin-top: 20px;
    align-items: flex-start;
    width: 80%;
}
.msg-box ul li{
    list-style-type: circle;
    line-height: 30px;
    margin-top: 10px;
    color: rgb(64, 64, 64);
    text-align: left;
}
.vertical-line{
    height: 250px;
    width: 2px;
    background-color: var(--mild-color);
}
@media screen and (max-width: 767px){
    .counter{
        display: none;
    }
    .msg-box{
        width: 100%;
        clip-path: polygon(5% 0, 100% 0%, 100% 100%, 66% 100%, 0 100%, 0 9%, 0 0);
        border-radius: 0px;
    }
    .ball{
        width: 30px;
        height: 30px;
    }
    .ball svg{
        font-size: 1rem;
    }
    .line-container{
        display: none;
    }
}