header{
    position: sticky;
    top: 0px;
    z-index: 2;
    background-color: transparent;
    width: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-self: flex-start;
    transition: all .5s ease-in-out;
}

.scroll-color{
    background-color: rgb(14,71,159);
    position: fixed;
    top: 0px;
}

nav {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

nav ul{
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
nav ul li{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.line{
    height: 1.5px;
    width: 0px;
    transition: all .3s ease;
    background-color: var(--primary-color);
}
nav ul li:hover .line{
    width: 100%;
}
nav ul li a {
    color:white;
    text-transform: capitalize;
    font-size: .9rem;
}
.signup-btn{
    padding: 10px 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:var(--primary-color);
    color: var(--secondary-color);
    text-transform: capitalize;
    border-radius: 5rem;
    font-family: poppins;
    cursor: pointer;
    transition: all .5s ease-in-out;
    font-weight: 500;
}

.logo{
    height: 40px;
    object-fit: contain;
    cursor: pointer;
}
@media (max-width: 990px) and (min-width: 768px){
    nav{
        display: none;
    }
}
@media screen and (max-width: 767px){
    nav{
        display: none;
    }
}