    width: 100%;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:svgjs='http://svgjs.com/svgjs' width='1440' height='700' preserveAspectRatio='none' viewBox='0 0 1440 700'%3e%3cg mask='url(%26quot%3b%23SvgjsMask1083%26quot%3b)' fill='none'%3e%3crect width='1440' height='700' x='0' y='0' fill='url(%23SvgjsLinearGradient1084)'%3e%3c/rect%3e%3cpath d='M8 700L708 0L1237.5 0L537.5 700z' fill='url(%23SvgjsLinearGradient1085)'%3e%3c/path%3e%3cpath d='M491.20000000000005 700L1191.2 0L1761.2 0L1061.2 700z' fill='url(%23SvgjsLinearGradient1085)'%3e%3c/path%3e%3cpath d='M1363 700L663 0L241.5 0L941.5 700z' fill='url(%23SvgjsLinearGradient1086)'%3e%3c/path%3e%3cpath d='M906.8 700L206.79999999999995 0L-418.70000000000005 0L281.29999999999995 700z' fill='url(%23SvgjsLinearGradient1086)'%3e%3c/path%3e%3cpath d='M884.3020002330094 700L1440 144.30200023300938L1440 700z' fill='url(%23SvgjsLinearGradient1085)'%3e%3c/path%3e%3cpath d='M0 700L555.6979997669906 700L 0 144.30200023300938z' fill='url(%23SvgjsLinearGradient1086)'%3e%3c/path%3e%3c/g%3e%3cdefs%3e%3cmask id='SvgjsMask1083'%3e%3crect width='1440' height='700' fill='white'%3e%3c/rect%3e%3c/mask%3e%3clinearGradient x1='87.15%25' y1='-26.43%25' x2='12.85%25' y2='126.43%25' gradientUnits='userSpaceOnUse' id='SvgjsLinearGradient1084'%3e%3cstop stop-color='%230e2a47' offset='0'%3e%3c/stop%3e%3cstop stop-color='%2300459e' offset='1'%3e%3c/stop%3e%3c/linearGradient%3e%3clinearGradient x1='0%25' y1='100%25' x2='100%25' y2='0%25' id='SvgjsLinearGradient1085'%3e%3cstop stop-color='rgba(15%2c 70%2c 185%2c 0.2)' offset='0'%3e%3c/stop%3e%3cstop stop-opacity='0' stop-color='rgba(15%2c 70%2c 185%2c 0.2)' offset='0.66'%3e%3c/stop%3e%3c/linearGradient%3e%3clinearGradient x1='100%25' y1='100%25' x2='0%25' y2='0%25' id='SvgjsLinearGradient1086'%3e%3cstop stop-color='rgba(15%2c 70%2c 185%2c 0.2)' offset='0'%3e%3c/stop%3e%3cstop stop-opacity='0' stop-color='rgba(15%