    background-position: bottom;
}
.landpage-content-wrapper{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-top: 70px;
    margin: auto;
}
.landpage-text-container{
    width: 45%;
    align-self: flex-start;
    text-align: left;
}
.landpage-text-container h1{
    color:var(--primary-color);
}
.landpage-text-container p{
    color: var(--mild-color);
    margin-top: 20px;
}
.landpage-img-container{
    width: 45%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}
.phone{
    height: 500px;
    width: 100%;
    object-fit: contain;
}
  .floating-container-img1{
    position: absolute;
    left: 0px;
    bottom: 130px;
    background-color: var(--secondary-color);
    padding: 10px 30px;
  }
  .floating-container-img1 img{
    height: 100px;
    object-fit: contain;
  }
  .floating-container-img2{
    position: absolute;
    padding: 10px 30px;
    right: 0px;
    top:0px;
    background-color: var(--secondary-color);
  }
  .floating-container-img3{
    position: absolute;
    padding: 10px 30px;
    right: -80px;
    bottom:-20px;
    background-color: var(--secondary-color);
  }
  @media (max-width: 990px) and (min-width: 768px){
