        justify-content: center;
        background-position: center;
    }
      .landpage-content-wrapper{
        flex-direction: column-reverse;
        justify-content: center;
      }
      .landpage-text-container{
        text-align: center;
        width: 80%;
        align-self: center;
      }
      .launch-btn{
        margin: 30px auto;
      }
      .landpage-img-container{
        margin-bottom: 50px;
        width: 90%;
      }
      .floating-container-img3{
        right: 10px;
      }
      .floating-container-img1{
        left: 10px;
        width: 300px;
      }
      .phone{
        width: 100%;
        width: 600px;
      }
  }
